<template>
  <div class="finance-new-list full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        class="scroll-view"
        :data="financeArr"
        :probeType="0"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div class="scroll-view-container">
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <pem-news-list :list="financeArr" :hot="hotwordsList"/>
          <div class="scroll-view-pullup">{{isMore ? '加载中...' : '暂时就这么多了...'}}</div>
        </div>
      </pem-scroll-view>
      <pem-operation-panel @backtotop="backtotop" />
      <pem-loading v-show="isLoading"></pem-loading>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemNewsList from 'components/pem-news-list/pem-news-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemNewsList,
    PemScrollView,
    PemLoading
  },
  data () {
    return {
      projectzj: '',
      currentPage: 1,
      totalPage: 0,
      pageSize: 10,
      financeArr: [],
      hotwordsList: [],
      isMore: true,
      isLoading: true
    }
  },
  watch: {
    '$route' (to, from) {
      if (this.$route.query.k) {
        this.getTopNewsList('init')
      }
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-头条')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.getTopNewsList('init')
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        this.getTopNewsList('more')
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.getTopNewsList('init')
      }
    },
    getTopNewsList (type = 'init') {
      this.isLoading = true
      if (type === 'init') {
        this.currentPage = 1
      } else {
        if (this.currentPage < this.totalPage) {
          this.currentPage = +this.currentPage + 1
        } else {
          this.isMore = false
          return
        }
      }
      let params = {
        currentPage: +this.currentPage,
        pageSize: +this.pageSize
      }
      if (this.$route.query.k) params.hottag = this.$route.query.k
      this._getTopNewsList(params, data => {
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = +data.currentPage || 1
          this.totalPage = +data.totalPage
          this.hotwordsList = data.hotwordsList || []
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _financeArr = (type === 'init') ? [] : this.financeArr
          _financeArr = _financeArr.concat(data.resultList || []).map(item => {
            if (item['icon'] && item.icon.indexOf(ImgPath) === -1) {
              item['icon'] = ImgPath + item['icon']
            }
            return item
          })
          this.financeArr = _financeArr
        }
      })
    },
    _getTopNewsList (options, callback) {
      MiniSiteApi.getTopNewsList(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

</style>



